<template>
  <div class="wrapDiv">
    <div class="top">
      <img :src="datas.img_detail_path" alt="" v-if="datas.img_detail_path" />
      <img v-else src="/static/images/bg_active_list.png" alt="" />
    </div>

    <div class="titleDiv">
      <div class="title">{{ datas.title }}</div>
      <div class="timeDiv">
        <span>类型：{{ datas.cate_name }}</span>
        <span>{{ datas.create_time | formatDate1 }}</span>
        <span> <i class="el-icon-view"></i> {{ datas.brower }} </span>
      </div>
    </div>

    <div class="content">
      <div v-html="datas.content"></div>
    </div>
  </div>
</template>

<script>
import { GET_ACTIVE_Details } from "@/api/mall";

export default {
  name: "activityDetails",
  data() {
    return {
      datas: {}
    };
  },
  methods: {
    detailData(ids) {
      GET_ACTIVE_Details({
        active_id: ids
      })
        .then(res => {
          this.datas = res.data;
        })
        .catch(err => {});
    }
  },
  mounted() {
    this.detailData(this.$route.query.id);
  }
};
</script>

<style lang="scss" type="text/scss" scoped>
.wrapDiv {
  width: 100%;
  height: 100%;
  .top {
    width: 100%;
    height: 694px;
    img {
      width: 100%;
      // min-width: 1080px;
      // height: 100%;
    }
  }
  .titleDiv {
    margin: 35px auto 0;
    width: 62.5%;
    text-align: left;
    .title {
      font-size: 36px;
      color: #000000;
    }
    .timeDiv {
      margin-top: 26px;
      color: #999999;
      font-size: 14px;
      span:nth-of-type(2) {
        margin: 0 60px 0 20px;
      }
    }
  }
  .content {
    margin: 51px auto 94px auto;
    width: 62.5%;
    // min-width: 1000px;
    color: #000000;
    font-size: 16px;
    img {
      width: 100%;
      height: 686px;
      margin: 34px 0;
    }
  }
}
/* 移动端  */
@media all and (max-width: 768px) {
  .wrapDiv {
    width: 100%;
    overflow: hidden;
    .top {
      width: 100%;
      height: auto;
      img {
        width: 100%;
      }
    }
    .titleDiv {
      margin: 35px auto 0;
      width: 100%;
      text-align: left;
      .title {
        font-size: 36px;
        color: #000000;
      }
      .timeDiv {
        margin-top: 26px;
        color: #999999;
        font-size: 14px;
        span:nth-of-type(2) {
          margin: 0 60px 0 20px;
        }
      }
    }
    .content {
      margin: 20px auto;
      width: 95%;
      // min-width: 1000px;
      color: #000000;
      font-size: 16px;
      ::v-deep img {
        width: 100% !important;
        height: auto !important;
      }
    }
  }
}
</style>



